	module.exports = {"data":{"markdownRemark":{"html":"<p>凌晨读《走下圣坛的周恩来》，说周恩来一次乘飞机去重庆，遇到恶劣天气，大难不死。</p>\n<p>上课，讲“法哲学原理”。黑格尔是理解西方政治思想的重要一环，要真正理解西方政治哲学之精神，必须读懂黑格尔。同时，马克思也受黑格尔的影响。主要是逻辑上的，内容上有革命性的变革。黑格尔对市民社会、国家、权力关系、法律、权利的论述值得好好分析研究。市民社会最近很热门，发表了大量文章，大概是对中国社会发展的一种西方式的透视，也是西方学者对1978年以来中国民主政治发展的一种反思。</p>\n<p>这种讨论影响到中国的学者，有的是为时髦，有的是为学术，有的是为政治。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0005.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0005.md","frontmatter":{"title":"1月8日 星期六"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0005.md absPath of file >>> MarkdownRemark"}}

